@use "../../scss/" as *;

.header {
  @include flex(center, space-between);
  height: $header-height;
  z-index: 10001;
  position: fixed;
  width: 100%;
  top: 0;
  right: 0;
  padding: 12px 8%;
  transition: 0.2s;
  background-color: $main-color;
  box-shadow: $box-shadow;
  // background-color: $white;
  &.shrink {
    height: $header-shrink-height;
    box-shadow: $box-shadow;
    @include mobile {
      height: $header-height;
    }
  }
  @include tablet {
    padding: 12px 4%;
  }
  &__wrap {
    @include flex(center, space-between);
    height: 100%;
    padding: 0 2rem;

    @include mobile {
      justify-content: center;
    }
  }
  .logo {
    font-size: 1.9rem;
    font-weight: 600;
    color: $txt-color;
    img{
      width: 5rem;
      filter: invert(100%);
      transition: 0.2s ease;
      &.sm-logo{
        width: 4rem;
      }
    }

    @include flex(center, center);
    height: inherit;
    li {
      padding: 5px 0;
      position: relative;
      &::after {
        content: "";
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translate(-50%);
        width: 0%;
        transition: width 0.3s ease;
        height: 4px;
        background-color: $white;
        @include mobile {
          color: $white;
        }
      }
      &:hover::after {
        width: 100%;
      }
      &:hover {
        color: $white;
        @include mobile {
          color: $white;
        }
      }
    }
  }
  #menu-icon {
    display: none;
    cursor: pointer;
    z-index: 1001;
    font-size: 24px;
    position: absolute;
    color: $white;
    margin: 0 2%;
    right: 0;
    @include mobile {
      display: block;
    }
  }

  &__nav {
    @include flex(center, center);

    li {
      padding: 5px 10px;
      margin: 10px;
      font-size: 1.2rem;
      color: $txt-color;
      font-weight: 500;
      position: relative;
      @include tablet {
        padding: 5px 2px;
      }
      &::after {
        content: "";
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translate(-50%);
        width: 0;
        transition: width 0.3s ease;
        height: 3px;
        background-color: $white;
      }
      &.active::after,
      &:hover::after {
        width: 100%;
      }
      &:hover {
        color: $white;
      }
    }

    @include mobile {
      position: absolute;
      top: $header-height;
      right: -100%;
      // right: 0;
      width: 100%;
      z-index: 20000;
      background-color: $darkgrey;
      flex-direction: column;
      transition:  0.6s cubic-bezier(0.18, 0.89, 0.32, 1.28);

      // background-color: #fff;
      box-shadow: $box-shadow;
      // text-align: left;
      &.active{
        // width: 100%;
        right: 0;
      }
    }
  }
}
